@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  flex-direction: row;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
  font-family: 'Balsamiq Sans', cursive;
}

.values {
  font-size: 24px;
  flex-direction: row;
  display: flex;
  align-items: center;
  width: 100px;
}

.input {
  margin-left: 10px;
  width: 40px;
  border-radius: 4px;
  border: 1px solid #868686;
  padding: 2px;
  height: 25px;
}

.val {
  margin-right: 5px;
  color: #454545;
}

.plus_sign {
  color: #29ac29;
  font-size: 1.6rem;
  margin-right: 5px;
}

.minus_sign {
  color: #ea3f45;
  font-size: 1.8rem;
  margin-left: 5px;
  margin-right: 5px;
}

.answer {
  margin-left: 20px;
}

.equal_sign {
  color: #6e6e6e;
}

.answer {
  padding: 5px 9px;
  border-radius: 40px;
}

.answerTrue {
  background: lightgreen;
  border: 2px solid green;
  color: green;
}

.answerFalse {
  padding: 2px 7px;
  background: #ee9090;
  border: 2px solid #571818;
}

.yourAnswer {
  font-size: 24px;
  color: #454545;
}

.rightAnswer {
  font-size: 20px;
  color: gray;
}

.form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffffad;
  padding: 20px;
  border-radius: 16px;
  border: 8px solid #00000014;
  font-family: 'Balsamiq Sans', cursive;
}

.submit_btn {
  margin-top: 50px;
  padding: 10px 25px;
  font-family: 'Balsamiq Sans', cursive;
  font-size: 24px;
  background: rgba(83, 162, 193, 0.97);
  border-radius: 4px;
  border: 1px solid #557fcd;
  color: #fff;
}

body {
  /*background: linear-gradient(to right, #a1c4fd 0%, #a1c4fd 100%);*/
  background: linear-gradient(to right, #6686bb 0%, #375382 100%);;
}

.App {
  text-align: center;
  flex: 1 1;
  display: flex;
  justify-content: center;
}

.greeting {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffffad;
  padding: 20px;
  border-radius: 16px;
  border: 8px solid #00000014;
  font-family: 'Balsamiq Sans', cursive;
}

.fieldItem {
  flex-direction: column;
  display: flex;
}

.roundInput {
  margin-top: 8px;
  border-radius: 4px;
  border: 1px solid #868686;
  padding: 2px;
  height: 25px;
  margin-bottom: 16px;
}

.submit_btn {
  margin-top: 20px;
  padding: 10px 25px;
  font-family: 'Balsamiq Sans', cursive;
  font-size: 24px;
  background: rgba(83, 162, 193, 0.97);
  border-radius: 4px;
  border: 1px solid #557fcd;
  color: #fff;
}

.container {
  display: flex;
  flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

